<template>
  <v-container>
    <v-layout
      column wrap align-center justify-center
    >
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-12">
          <v-toolbar dark color="light-green darken-2">
            <v-toolbar-title>New Employee</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form lazy-validation ref="uForm">
              <v-text-field
                v-model="employeeId"
                label="Employee Id"
              ></v-text-field>
              <v-text-field
                v-model="name"
                label="name"
              ></v-text-field>
              <v-text-field
                v-model="dept"
                label="department"
              ></v-text-field>
              <v-text-field
                v-model="position"
                label="position"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn flat color="red lighten-1" to="/">back</v-btn>
            <v-spacer />
            <v-btn color="light-green darken-2" round dark large  @click="saveEmployee()"
              >submit
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>


<script>
  import db from "./firebaseInit";
  import * as firebase from 'firebase';
  export default {
    name: 'new-employee',
    data () {
      return {
        employeeId: null,
        name: null,
        dept: null,
        position: null,
        create: null
      }
    },
    methods: {
     saveEmployee () {
       this.$refs.uForm.validate();
       db.collection('employees').add({
         employeeId: this.employeeId,
         name: this.name,
         dept: this.dept,
         position: this.position,
         create: firebase.firestore.FieldValue.serverTimestamp()
       })
       .then(docRef => this.$router.push('/'))
       .catch(error => console.log(err))
      }
    }
  }
</script>


